import Vue from 'vue'
import App from './App.vue'
import jQuery from 'jquery'
import router from "./router";
import moment from 'moment';
import store from "./store";
import VueHead from 'vue-head'
import gv from './mixins/grobalValiables'
// import './registerServiceWorker'

Vue.mixin(gv)    // mixin

// -----  jQuery -------------
global.jquery = jQuery
global.$ = jQuery
window.$ = window.jQuery = require('jquery')


// -----  SASS -------------
require('@/sass/style.scss')

// -----  Filters -----------
// 
Vue.filter('translate_to_jp_age', function (value) {
  switch (value) {
    case "e_10s":
      return "10"
    case "l_10s":
      return "10"
    case "e_20s":
      return "20"
    case "l_20s":
      return "20"
    case "e_30s":
      return "30"
    case "l_30s":
      return "30"
    case "e_40s":
      return "40"
    case "l_40s":
      return "40"
    case "e_50s":
      return "50"
    case "l_50s":
      return "50"
    case "e_60s":
      return "60"
    case "l_60s":
      return "60"
    default:
      return value
  }
});
Vue.filter('translate_to_jp_age_simple', function (value) {
  switch (value) {
    case "10s":
      return "10"
    case "20s":
      return "20"
    case "30s":
      return "30"
    case "40s":
      return "40"
    case "50s":
      return "50"
    case "60s":
      return "60"
    default:
      return value
  }
});
Vue.filter('translate_to_jp_sex', function (value) {
  switch (value) {
    case "m":
      return ""
    case "f":
      return ""
    case "o":
      return ""
    default:
      return value
  }
});
Vue.filter('format_date', function (value) {
  return moment(value).format('YYYY/MM/DD HH:mm')
}
);

// ----- scroll -------------
(function () {
  const targets = global.$(".t-wrapper");

  // Get suit scroll event
  const pcScrollEvent = (() => {
    if ("onwheel" in document) return "wheel";
    if ("onmousewheel" in document) return "mousewheel";
    return "DOMMouseScroll";
  })();

  const preventDefault = event => {
    event.preventDefault();
    return false;
  };

  // 
  // 
  let scrollTop = 0;

  // 
  const freeze = () => {
    scrollTop = global.$(window).scrollTop();
    targets.css({ position: "fixed" });
  };

  // 
  const freezeAll = () => {
    [pcScrollEvent, "touchmove"].forEach(event => {
      document.addEventListener(event, preventDefault, { passive: false });
    });
  };

  // 
  const allow = () => {
    targets.css({ position: "" });
    global.$("html,body").scrollTop(scrollTop);
  };

  // 
  const allowAll = () => {
    [pcScrollEvent, "touchmove"].forEach(event => {
      document.removeEventListener(event, preventDefault);
    });
  };

  return {
    freeze,
    freezeAll,
    allow,
    allowAll
  };
})();

global.$(function () {
  /***********************************
   * 
   ***********************************/

  // 25px
  //   .o-global-header  .is-scrolled 
  // 
  //   .o-global-header  .is-scrolled 
  global.$(window).on("scroll", () => {
    const currentPageYOffset = window.pageYOffset;
    if (currentPageYOffset >= 25)
      global.$(".o-global-header").addClass("is-scrolled");
    if (currentPageYOffset <= 0)
      global.$(".o-global-header").removeClass("is-scrolled");
  });

});


// -----  VueHead ------------
Vue.use(VueHead, {
  separator: ' | '
})

// -----  Vue -------------
Vue.config.productionTip = false
new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
